export function Card({ title, imgSm, imgLg }) {
  const width = window.innerWidth;
  const url = `/assets/${width > 768 ? imgLg : imgSm}`;
  return (
    <div class="card-wrapper">
      <div class="container content">
        <div class="row center">
          <h5 class="header col s12">{title}</h5>
        </div>
      </div>
      <img src={url} alt="Unsplashed background img 3" />
    </div>
  );
}

